import React from 'react';
import { Typography, Paper, Divider } from '@material-ui/core';
import { makeStyles, createStyles } from '@material-ui/core/styles';
const useStyles = makeStyles((theme) => createStyles({
  marginChildren: {
    overflow:'hidden',
    margin: `${theme.spacing(1)}px ${theme.spacing(0.5)}px`,
    '& > *': {
      margin: `${theme.spacing(2)}px ${theme.spacing(2)}px`,
      fontSize: '1.175rem'
    }
  }
}))

const companyName = 'Pixar Animation Studios'
const title = 'Software Developer, RenderMan'
const paragraphs = [
  "My adoration for Pixar's ability to craft stories in no small part due to its extreme technical excellence. Every Pixar movie I see is somehow even more stunning than the last. Having an opportunity to collaborate with the individuals who make the impossible possible seems like a dream, which is why this opportunity is exciting to me.",
  "I am looking for an opportunity to move into a scrappy environment with passionate developers to create something beautiful. Just like clear graphical interfaces make it easier for end-users to become accustomed to products, well-crafted code helps developers producer more features quickly. I am extremely meticulous when it comes to coding standards, and believe this quality helps to rapidly prototype and ship features and bug fixes to stakeholders.",
  "In addition, I believe I am a perfect fit for this position due to my experience working with low-level tools like OpenGL and GLSL, as well as higher-level tools like the Unity Shader Graph. I also am legitimately passionate about these technologies. There is something extremely magical about getting all the math to line up just right, and seeing your model or effect brought to life.",
]

const CoverLetter = () => {
  const [day, month, year] = new Date().toUTCString().split(" ").splice(1,3)
  const classes = useStyles()
  return (
    <Paper className={`${classes.marginChildren}`}>
      <Typography>
        {`${day} ${month}, ${year}`}
        <br/>
        {companyName}
        <br/>
        {title}
      <Divider/>
      </Typography>
      <Typography> Dear Hiring Manager,</Typography>
      {paragraphs.map(paragraph=>(
        <Typography>{paragraph}</Typography>
      ))}
      <Typography>
        Thank you for your consideration,
        <br></br>
        Robert Aron
      </Typography>
    </Paper>
  )
}

export default CoverLetter